.sidebar {
  position: absolute;
  left: 3.5vw;
  color: white;
  padding: 0;
  top: 11vh;
  height: 111.75vh;
  width:  16.5vw;
  background: #008080;
  /* border: 1px solid red; */
}

.sidebar .menuUL {
  margin: 0 0 2vh 0;
  padding: 2vh 1vw 2vh 1vw;
  font-size: .8rem;
  background: #47423f;
  text-align: left;
  width: 14.5vw;
  /* border: 1px dotted white; */

}

.sidebar .menuUL li {
  list-style-type: none;
  padding: .5vh 0 .5vh 1vw;
  color: white;
  border-top-left-radius: 20%;
  border-bottom-left-radius: 20%;
  /* border-bottom: 1px solid #008080; */
  /* border-bottom: 1px solid transparent; */
  background-image: -webkit-gradient(linear, left top, right top, from(#008080),to(#47423f));
  background-image: -webkit-linear-gradient(left, #008080,#47423f);
  background-image: -o-linear-gradient(left, #008080,#47423f);
  background-image: linear-gradient(left, #008080,#47423f);
  margin: .75vh 0 .5vh 0;
}

.sidebar .menuUL li:hover{
  cursor: pointer;
}

.sidebar .menuUL li:last-child {
  border-bottom: none;
}

.sidebar .infoComponentBox{
  color: white;
  /* border: 1px dotted white; */
  height: 12.5vh;
  background: #47423f;
  font-size: .7rem;
  margin: 4vh 0 4vh 0;
  padding: .25vh 0 1vh 0;
}

.sidebar .infoComponentBox h5 {
  margin: .25vh 0 .25vh 0;
  padding: 0 0 0 .75vw;
  font-size: .75rem;
  height: 2vh;
}

.sidebar .infoComponentBox p {
  margin: .25vh 1vw 0 1vw;
  font-size: .8rem;
}

.sidebar .sidebarBlurb {
  /* border: 1px dotted white; */
  background: #47423f;
  padding: 1vh .5vw 1vh .5vw;
  height: 20vh;
  margin: 0 0 4vh 0;
  font-size: .7125rem;
}

.sidebar .sidebarBlurb h5 {
  padding: 0 0 0 .15vw;
  text-align: left;
  font-size: 1rem;
}

.sidebar .sidebarBlurb p {
  margin: .5vh 0 0 0;
  text-align: left;
}

.sidebar .socialMedia {
  /* border: 1px dotted white; */
  background: #47423f;
  width: 100%;
  height: 34.75vh;
}

.sidebar .socialMedia ul {
  padding: 7.5% 0 0 6%;
}

.sidebar .socialMedia ul li {
  float: left;
  list-style-type: none;
  height: 70%;
  width:40.83%;
  margin: auto 6% auto auto;
}

.sidebar .socialMedia ul li button{
  background: transparent;
  border: none;
  height: 100%;
  width: 100%;
}

.sidebar .socialMedia ul li button:focus {
  outline: 0;
}

.sidebar .socialMedia li img {
  height: 100%;
  width: 100%;
}

#fbButton{
  height: 100%;
  width: 100%;
}

#fbButton a{
  height: 100%;
  margin: 0;
  width: 100%;
}

#igButton {
  height: 100%;
  width: 100%;
}

#igButton a{
  height: 100%;
  margin: 0;
  width: 100%;
}


#houzzButton{
  height: 100%;
  width: 100%;
}

#houzzButton a{
  height: 100%;
  margin: 0;
  width: 100%;
}

#pinterestButton {
  height: 100%;
  width: 100%;
}

#pinterestButton a{
  height: 100%;
  margin: 0;
  width: 100%;
}



